* {
  margin-top: 0;
}
.header {
  position: fixed;
  background-color: #ffffff99;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  height: 2.5vw;
  width: 100%;
  border-bottom: 1px solid #00000015;
  z-index: 3;
  box-shadow: 0px 5px 8px #00000015;
}

.logo {
  height: 2.5vw;
  margin-left: 0.5vw;
  transition: all 0.2s ease-in-out;
}
.logo:hover {
  transform: scale(1.1);
  transition: all 0.3s ease-in-out;
}
.links {
  height: 1vw;
  margin-right: 2vw;
  transition: all 0.2s ease-in-out;
  margin-bottom: 2vw;
  z-index: 4;
  /*box-shadow: 10px 10px 10px #00000030;*/
}
.links:hover {
  opacity: 1;
  transform: scale(1.15);
  transform: translate(0, -5px);
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
@media screen and (max-width: 600px) {
  .header{
    position: fixed;
    height: 7.5vw;
    width: 100%;
    top: 0;
  }
  .logo {
    height: 7.5vw;
  }
  .links{
    width: 3.5vw;
    margin-top: 1vw;
    margin-left: 2vw;
    margin-right: 2vw;
    height: auto;
    float: left;
  }
}