.homesection{
  height: 55vw;
}

.navicons {
  position: fixed;
  width: 2.5vw;
  top: 32vw;
  left: 1vw;
  z-index: 3;

}
.navicons img {
  margin-bottom: 0.5vw;
  width: 2vw;
  transition: all 0.2s ease-in-out;
  opacity: 50%;
  cursor: pointer;
}
.mahadvector {
  position: absolute;
  top: 30vw;
  left: 75vw;
  height: 17.5vw;
}
@media screen and (max-width: 600px) {
  .homesection{
    width: 100vw;
    height: 170vw;
  }
  .mahadvector{
    margin-top: 60vw;
    left: 55vw;
    height: 70vw;
    z-index: 2;
  }
  .navicons {
    visibility: hidden;
    display: none;
  }
  
}
@media screen and (max-width: 400px) {
  .homesection{
    width: 100vw;
    height: 170vw;
  }
  .mahadvector{
    margin-top: 45vw;
    left: 20vw;
    height: 100vw;
    z-index: 2;
  }
  .navicons {
    visibility: hidden;
    display: none;
  }
}