html {
  scroll-behavior: smooth;
  width: 100%;
  overflow-x: hidden;
}
body{
  overflow-x: hidden;
  width: 100%;
}
@font-face {
  font-family: Circe;
  src: url('./CRC55.otf');
}
@font-face {
  font-family: Circe;
  font-weight: bold;
  src: url('./CRC65.otf');
}
@font-face {
  font-family: Circe;
  font-weight: lighter;
  src: url('./CRC35.otf');
}
*{

  font-family: Circe;
  margin-left: 0;
  margin-right: 0;
}
#animation{
  position: fixed;
  z-index: 5;
  width: 100%;
  animation: animation 0.5s 1;
  -webkit-animation:animation 0.5s 1;
  animation-fill-mode: forwards;

  animation-delay:3s;
  -webkit-animation-delay:3s; /* Safari and Chrome */
  -webkit-animation-fill-mode: forwards;
}
@keyframes animation{
  from {opacity :1; z-index: 5;}
  to {opacity :0; z-index: 0;}
}

@-webkit-keyframes animation{
  from {opacity :1; z-index: 5;}
  to {opacity :0; z-index: 0;}
}

.arrowicon{
  animation-name: example;
  animation-iteration-count:infinite;
  position: absolute;
  top: 49.5vw;
  left: 47vw;
  width: 5vw;
  opacity: 0.5;
}
.sectiontitle hr{
  display: inline-block;
  width: 35vw;
}
.sectiontitle{
  margin-top: 7vw;
  margin-bottom: 2.5vw;
  text-align: center;

}
.sectiontitle h2{
  display: inline-block;
  line-height: 0%;
  font-size: 2vw;
  margin-left: 1vw;
  margin-right: 1vw;
}
.sectiontitle img{
  display: inline-block;
  margin-right: 1vw;
  width: 1.5vw;
}
.aboutsection{
  margin: 0 auto; 
  width: 60vw;
  height: 45vw;
}
.aboutsection h1{
  text-align: center;
  opacity: 0.8;
  font-weight: 600;
  font-size: 2.5vw;
}
.aboutsection h2{
  text-align: center;
  opacity: 0.8;
  font-weight: 600;
  font-size: 1.5vw;
}
.projectssection{
  margin-left: 20vw;
  margin-right: 20vw;
  height: 107.5vw;
}
.portfoliosection{
  margin-left: 20vw;
  width: 70vw;
  height: 60vw;
}
.getintouchsection{
  width: 70vw;
  height: 20vw;
  margin-left: 15vw;
  margin-right: 15vw;
}
.getintouchsection h1{
  font-size: 5vw;
  float: left;
}
.getintouchsection p{
  margin-top: -2vw;
  margin-bottom: 10vw;
  float: left;
  margin-left: 3vw;
  font-weight: lighter;
  font-size: 1.5vw;
  opacity: .50;
  float: left;
}
.footersection{
  width: 22.5vw;
  height: 3vw;
  margin: 0 auto;
}
.footersection h1{
  font-size: 0.75vw;
}
@media screen and (max-width: 600px) {
  .aboutsection{
    width: 90vw;
    height: 130vw;
    margin-top: 7.5vw;
  }
  .aboutsection h1{
    padding-top: 4vw;
    font-size: 4.25vw;
  }
  .aboutsection h2{
    font-size: 3.5vw;
  }
  .aboutsection hr{
    margin-left: 3vw;
    margin-top: 5vw;
  }
  .projectssection{
    margin-left: 10vw;
    margin-right: 10vw;
    width: 100vw;
    height: 440vw;
  }
  .portfoliosection{
    margin-left: 10vw;
    margin-right: 10vw;
    width: 90vw;
    height: 350vw;
  }
  .portfoliosection p{
    opacity: .50;
  }
  .sectiontitle{
    width: 200vw;
    margin-left: -50vw;
    margin-bottom: 8vw;
  }
  .sectiontitle h2{
    display: inline-block;
    line-height: 0%;
    font-size: 6vw;
    margin-left: 2vw;
    margin-right: 2vw;
  }
  .sectiontitle img{
    display: inline-block;
    margin-right: 2vw;
    width: 4vw;
  }
  .sectiontitle hr{
    display: inline-block;
    width: 50vw;
  }
  .getintouchsection{
    width: 90vw;
    height: 50vw;
    margin-left: 10vw;
    margin-right: 10vw;
  }
  .getintouchsection hr{
    margin-right: 10vw;
  }
  .getintouchsection h1{
    font-size: 10vw;
    margin-top: 2vw;
  }
  .getintouchsection p{
    margin-top: -2vw;
    font-size: 3.25vw;
    margin-left: 5vw;
    margin-right: 10vw;
    text-align: justify;
  }
  .footersection{
    width: 70vw;
    height: 10vw;
    margin: 0 auto;
    text-align: center;
  }
  .footersection h1{
    font-size: 2.5vw;
  }
}
@media screen and (max-width: 400px) {
  .aboutsection{
    width: 90vw;
    height: 130vw;
    margin-top: 55vw;
    overflow: auto hidden;
  }
}